@use "base";

.carrousel {
    position: relative;
    &__container {
        opacity: 0.3;
        transition: all 1s ease-in-out;
        &.active {
            opacity: 1;
        }
        &--background {
            border-radius: 15px;
            width: 100%;
            height: 415px;
            object-fit: cover;
    }
}
    &__left {
        position: absolute;
        top: 40%;
        left: 0;
        &:hover {
            cursor: pointer;
        }
    }
    &__right {
        position: absolute;
        top: 40%;
        right: 0px;
        &:hover {
            cursor: pointer;
        }
    }
    p {
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 18px;
        color: base.$white-color;
    }
    @media (max-width: 768px) {
        &__container {
            &--background {
                height: 255px;
            }
        }
        &__left {
            width: 32px;
            height: 40px;
        }
        &__right {
            width: 32px;
            height: 40px;
        }
        p {
            display: none;
        }
    }
}